import { IconButton, Menu, MenuItem } from '@material-ui/core';
import React from 'react';
import { FaUserCircle } from 'react-icons/fa';

function DashboardProfileMenu() {
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

  function onProfileMenuClick(evt: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    setAnchorEl(evt.currentTarget);
  }

  function onMenuClose() {
    setAnchorEl(null);
  }

  return (
    <>
      <IconButton
        aria-label="more"
        aria-haspopup="true"
        onClick={onProfileMenuClick}
      >
        <FaUserCircle />
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        getContentAnchorEl={null}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={onMenuClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <MenuItem onClick={onMenuClose}>Profile</MenuItem>
        <MenuItem onClick={onMenuClose}>Logout</MenuItem>
      </Menu>
    </>
  )
}

export default DashboardProfileMenu;