import styled from '@emotion/styled';
import React, { PropsWithChildren } from 'react';

const Title = styled.div`
  font-size: 20px;
  margin-bottom: 16px;
  flex-grow: 1;
`;

const Head = styled.div`
  display: flex;
`;

const Root = styled.div`

  @media (min-width: 600px) {
    border: 2px solid #eee;
    padding: 16px;
  }
  @media (max-width: 599.95px) {
    padding: 0;
    border-bottom: 2px solid #eee;
  }
`;

interface CardProps {
  title?: string;
  ariaLabel?: string;
  actionMenu?: React.ReactNode;
}

// const Card: React.ForwardRefRenderFunction<PropsWithChildren<CardProps>, HTMLDivElement | null | any> = ({
function Card({
  children,
  title,
  ariaLabel,
  actionMenu,
}: PropsWithChildren<CardProps>) {
// }, ref) => {
  return (
    <Root aria-label={ariaLabel}>
      {
      title &&
        <Head>
          <Title>{title}</Title>
        </Head>
      }
      {children}</Root>
  )
}

export default Card;