import React from 'react';
import { useHistory } from 'react-router-dom';
import styled from '@emotion/styled';
import { Button, Dialog, DialogContent, DialogTitle } from '@material-ui/core';
import Card from './Card';
import PaddedContainer from './PaddedContainer';

const Actions = styled.div`
  /* text-align: center; */
`;

const CenteredContent = styled.div`
  text-align: center;
  margin-bottom: 20px;
`;

const Intro = styled.p`
  margin: 20px 0;
`;

const XeroImage = styled.img`
  width: 100px;
  height: 70px;
  margin: 20px 0;
`;

function Accounts() {
  const history = useHistory();
  const [isOpen, setIsOpen] = React.useState(false);


  function onDialogClose() {
    setIsOpen(false);
  }

  function onGetStartedClick() {
    setIsOpen(s => !s);
  }

  function onConnectClick() {
    setIsOpen(false);
    history.push('/account/100');
  }

  return (
    <PaddedContainer>
      <Card title="Are you ready for financial freedom?">
        <Actions>
          <Button variant="contained" onClick={onGetStartedClick}>
            Get Started
          </Button>
        </Actions>
        <Dialog
          open={isOpen}
          onClose={onDialogClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">
            Create Account
          </DialogTitle>
          <DialogContent>
            <CenteredContent>
              <Intro>
                APositive connects to your accounting package to streamline the application process.
              </Intro>
              <XeroImage
                src="https://static.apos-test.com.au/client-au/integration/9.png"
                alt="Xero"
              />
              <div>
                <Button variant="contained" onClick={onConnectClick}>
                  Connect
                </Button>
              </div>
            </CenteredContent>
          </DialogContent>
        </Dialog>
      </Card>
    </PaddedContainer>
  )
}

export default Accounts;